*, *:after, *:before {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    /*transition: .1s ease-in-out;  добавим плавность переходов для всех элементов страницы*/
}

body {

    height: 100%;
    background: #f6f6fa;
}


input {
    border: none;
    border-radius: 25px;
    background-color: #f6f6fa;
    outline: none;
    padding: 5px;
}


input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus
input:-webkit-autofill,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus  {
    -webkit-box-shadow: 0 0 0 30px #f6f6fa inset !important;
    transition: background-color 5000s ease-in-out 0s;
}


.mainPage {
    width: 100%;
    display: grid;
    grid-template-areas: ". icon userLog ."
                         ". navbar wraper .";
    grid-template-rows: 88px auto;
    grid-template-columns: 11% 16% 59% 11%;
    grid-row-gap: 1em;
    grid-column-gap: 1%;
    /*
    grid-column-gap: 0.3em;
    grid-row-gap: 0.2em;
    */
}

.head {
    width: calc(100vw - 10px);
    height: 88px;
    box-shadow: 0 10px 10px 0 rgba(0, 0, 0, 0.05);
    background-color: #1a122d;
    z-index: -1;
}

.icon {
    grid-area: icon;
    align-self: center;
}

.nav {
    grid-area: navbar;
    width: 100%;
    height: 504px;
    border-radius: 10px;
    background-color: white;
}

.userLog {
    grid-area: userLog;
    align-self: center;
}

.appWrapperContent {
    grid-area: wraper;
    width: 100%;
}

.preloader {
    margin-left: 20px;
}


