.nav {
    margin-left: 12%;
    height: 500px;
}

.link {
    display: grid;
    margin-top: 40px;
    grid-template-areas: "ico label";
    grid-template-columns: 18px auto;
    grid-gap: 4.6%;
}

.link svg {
    grid-area: ico;
    width: 100%;
    fill: #9d9aa8;
}

.activeItem svg{
    fill: #6d57f6;
}

.item {
    grid-area: label;
    font-family: Montserrat;
    max-width: 120px;
    height: 18px;
    font-size: 14px;
    font-weight: 500;
    line-height: 1.29;
    text-align: left;
    color: #9d9aa8;
}

.activeItem .item{
    color: #6d57f6;
    font-weight: 600;
}

.itemLogout svg {
    fill: #f81c1c;
    cursor: pointer;
}

.itemLogout .item {
    color: #f81c1c;
    cursor: pointer;
}


