.login {
    width: 100%;
    height: 100%;
    padding: 3.5%;
    background-color: white;
    border-radius: 10px;
}

.button {
    width: 60px;
    height: 25px;
    background-color: #6d57f6;
    border-radius: 25px;
    border: none;
    cursor: pointer;
    outline: none;
    font-size: 14px;
    color: white;
    margin-top: 5px;
}

.s1 {
    display: grid;
    width: 120px;
    grid-template-areas: "so st";
    grid-template-columns: auto auto;
}

.s2 {
    grid-area: so;
}

.s3 {
    grid-area: st;
    margin-top: -2px
}

